// =============================================================================
// USER PLANS API
// =============================================================================
// GET /api/v1/user/plans - List available plans
// Returns all plans the user can upgrade/downgrade to

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { getDb } from '@/db/client';

export async function GET(request: NextRequest) {
  // Require authentication
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  try {
    const db = getDb();

    // Get all active plans
    const result = await db.query<{
      id: string;
      name: string;
      description: string | null;
      daily_limit: number;
      monthly_limit: number;
      price_cents: number;
      is_active: boolean;
    }>(`
      SELECT id, name, description, daily_limit, monthly_limit, price_cents, is_active
      FROM plans
      WHERE is_active = true
      ORDER BY price_cents ASC
    `);

    const plans = result.rows.map((row) => ({
      id: row.id,
      name: row.name,
      description: row.description,
      dailyLimit: row.daily_limit,
      monthlyLimit: row.monthly_limit,
      priceCents: row.price_cents,
      priceDisplay: row.price_cents === 0 ? 'Free' : `$${(row.price_cents / 100).toFixed(0)}/mo`,
      features: getPlanFeatures(row.id),
    }));

    return NextResponse.json({
      success: true,
      plans,
    });
  } catch (error: any) {
    console.error('[user/plans] Error:', error);
    return NextResponse.json(
      {
        error: 'Failed to fetch plans',
        code: 'INTERNAL_ERROR',
      },
      { status: 500 }
    );
  }
}

/**
 * Get feature list for each plan (for display in upgrade modal)
 */
function getPlanFeatures(planId: string): string[] {
  const features: Record<string, string[]> = {
    free: [
      '1,000 API calls/day',
      '10,000 API calls/month',
      'Community support',
      'Basic analytics',
    ],
    starter: [
      '10,000 API calls/day',
      '100,000 API calls/month',
      'Email support',
      'Advanced analytics',
      'Webhook integrations',
    ],
    pro: [
      '100,000 API calls/day',
      '1,000,000 API calls/month',
      'Priority support',
      'Full analytics suite',
      'Custom webhooks',
      'API key management',
      'Team members (up to 5)',
    ],
    enterprise: [
      'Unlimited API calls',
      'Custom limits',
      'Dedicated support',
      'SLA guarantee',
      'Custom integrations',
      'Unlimited team members',
      'On-premise option',
    ],
  };

  return features[planId] || [];
}

